
@media screen and (max-width: 974px) {
 
body{
    overflow-x: hidden;
}

.Homepage_section1{
    height: 100%;

.hero{
    height: auto;
}

.hero__text-container{

    height: auto ;
}

    .hero__text{
        margin-left: 0;
    }
}

// .scroll_container{
//     width: 100%;
//     position: relative;
//     top:0;
//     overflow:hidden;
// }



.images{
    img{
        height: 87vh;
        width: 80vw;
    }
}

.row{
    flex-direction: column;
}

.T-right-5{
    right: 5rem !important;
}


.container-left{
    width: 80%;
    margin: auto;
    overflow: hidden;
}

.obj_fit-cover{
    object-fit: cover; 

}

.hide{
  display: none !important;
}

.page_image{
    margin-top: 15rem;

    .image{
        width: 100vw;
        height: 120vh;
    }
}

.tab_column{
    flex-direction: column;
}

.pos_none{
    position: unset !important;
}

.width_50{
    width: 50%;
}

.width_80{
    width: 80% !important;
}

.d-block
{
    display: block;
}


.hide-large{
  display: inline-block;
}


.btn-sm{
    display: inline-block !important; 
    border: 1px solid $tertiary-color;
    padding: 1.5rem 3rem;
    margin: 1rem 0;
    text-align: center;
}

.width_100{
    width: 100% !important;
}

.mr-auto{
    margin-right: auto !important;
}

.my-5{
    margin: 5rem 0;
}


.tab_mt-15{margin-top: 15rem;}
.tab_color-black{color: $black-color !important;}
.tab_text{width: 100% !important;}
}



@media screen and (max-width: 600px)
{

.page_image{
    margin-top: 5rem;

    .image{
        width: 100vw;
        height: 60vh;
    }
}


    .obj_fit-cover{
        object-fit: contain !important;
    }

    .M-right-1{
        right: 1rem !important;
    }
}